nav{
    width:100%;
    
    /*background:#005069 ;*/
    color: #ffffff;
    padding: 4px 0;
    position: fixed;
    top:0;
    left:0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;

} 
.logo{
    width:80px;
    
}
nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;


}
.dark-nav{
    background:#005069 ;
    transition: 0.5s;
}