@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Outfit", serif;
}
body{
    background:floralwhite ;
}
a{
    text-decoration: none;
    color: inherit;
    line-height: 1;
    cursor: pointer; 
}
.container{
    padding-left: 10%;
    padding-right: 10%;
}
.btn{
    background: #ffffff;
    color:#005069;
    padding: 14px 25px;
    font-size: 16px;
    border-radius: 30px;
    cursor: pointer;
    border:0;
    outline: 0;
}