@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap);
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Outfit", serif;
}
body{
    background:floralwhite ;
}
a{
    text-decoration: none;
    color: inherit;
    line-height: 1;
    cursor: pointer; 
}
.container{
    padding-left: 10%;
    padding-right: 10%;
}
.btn{
    background: #ffffff;
    color:#005069;
    padding: 14px 25px;
    font-size: 16px;
    border-radius: 30px;
    cursor: pointer;
    border:0;
    outline: 0;
}

nav{
    width:100%;
    
    /*background:#005069 ;*/
    color: #ffffff;
    padding: 4px 0;
    position: fixed;
    top:0;
    left:0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;

} 
.logo{
    width:80px;
    
}
nav ul li{
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;


}
.dark-nav{
    background:#005069 ;
    transition: 0.5s;
}
.login{
  width: 100%;
    min-height: 100vh;
    background: linear-gradient(rgba(0, 81, 105, 0.7),rgba(0, 81, 105, 0.7)),url(/senujaBodhinayake/itinerary-travel-plannerstatic/media/35bcb890318dfda41ecf85f859e1e863.d551f129.jpg);
    background-size: cover;
    background-position: center;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
}
.login-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;
    color: #333;
  }
  
  /* Style the form inputs */
  input {
    width: 100%;
    padding: 12px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
  }
  
  input:focus {
    border-color: #005069;
    outline: none;
  }
  
  /* Button styling */
  button {
    width: 100%;
    padding: 12px;
    background-color: #005069;
    border: none;
    color: white;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #09b9ee;
  }
  
  /* Toggle link styling */
  p {
    text-align: center;
    font-size: 14px;
    color: black;
  }
  
  span {
    color: #4CAF50;
    cursor: pointer;
  }
  
  span:hover {
    text-decoration: underline;
  }
 .hero{
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(rgba(0, 81, 105, 0.7),rgba(0, 81, 105, 0.7)),url(/senujaBodhinayake/itinerary-travel-plannerstatic/media/35bcb890318dfda41ecf85f859e1e863.d551f129.jpg);
    background-size: cover;
    background-position: center;
    color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;

 }
 .hero-text{
    text-align: center;
    max-width: 800px;
 }
 .hero-text p{
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4; 
 }
 .hero-text h1{
    font-size: 60px;
    font-weight: 800px;
 }
 .btn2{
   background: #ffffff;
    color:#005069;
    padding: 14px 25px;
    font-size: 16px;
    border-radius: 30px;
    cursor: pointer;
    border:0;
    outline: 0;
    width: 50%;
 }
.categories{
    margin: 80px auto;
    
    width: 90%;
    display:flex;
    
    align-items: center;
    justify-content: space-between;
    
}
.category img{
    width:100%;
    height: 200px;
    
    border-radius: 10px;
    
    
    display: block;
    

}
.category{
    flex-basis: 31%;
    position: relative;
    
    
    
}
.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background:rgba(0, 81, 105, 0.3); 
    display: flex;
    align-items: center;
    justify-content:center;
    flex-direction: column;
    color: #ffffff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;



}
.caption img{
    width: 60px;
    height: 60px;
    margin-bottom: 10;

}
.category:hover .caption{
    opacity: 1;
    padding-top:0 ;
}
.title{
    text-align:center;
    color: #005069;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
    margin: 70px 0 30px;


}
.title h2{
    font-size: 32px;
    color: #005069;
    margin-top: 5px;
    text-transform: none;
}
.about{
    margin: 100px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width:90%;
}
.about-left{
   flex-basis: 40%;

}
.about-right{
    flex-basis: 56%;
}
.about img{
    width: 100%;
    border-radius: 10px;
}
.about h3{
    font-weight: 600;
    font-size: 16px;
    color: #005069;
}
.about h2{
    
    font-size: 35px;
    color: #005069;
    margin: 10px 0;
    max-width: 400px;
    
}
.about p{
   color:#002e3c;
   margin-bottom: 15px;
}

.upcountry{
    margin: 80px auto;
    
    width: 90%;
    display:flex;
    
    align-items: center;
    justify-content: space-between;
}
.destination img{
    margin-top: 10px;
    width:100%;
    height: 200px;
    
    border-radius: 10px;
    
    
    display: block;
    
}
.destination{
    flex-basis: 31%;
    position: relative;
}
.destinations h3{
    font-size: 32px;
    color: #005069;
    margin-top: 5px;
    text-transform: none;

    

}
.destinations h4{
    text-align: left;
    margin-top:0;
    margin-bottom: 10px;
    color: #005069;
    
    

}



.itinerary-container{
    max-width: 800px;
    margin: 50px auto;
    padding: 20px;
    background: white;
    box-shadow: 0px 4px 10px rgba(0,0,0,0.1);
    border-radius: 10px;
    text-align: center;
}
.title{
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
}
.date-selection{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.date-input{
    padding: 10px;
    width:48%;
    border: 1px solid #ccc;
    border-radius: 5px;
}
.destination-input{
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    margin-bottom: 20px;
    
}
.input-field{
    flex: 1 1;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    
}
.add-button{
    background: #005069;
    max-width: 20%;
    margin-top: 10px;
    margin-bottom: 10px;
    color: white;
    border:none;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
}
.add-button:hover{
    background: #0056b3;
}
.itinerary-list{
    margin-bottom: 20px;
}
.destination-item{
    background: #f8f9fa;
    padding: 10px;
    border-radius: 5px;
    margin-bottom: 5px;
}

.placeholder-text{
    color:#888;
    font-size: 14px;  
}
.save-button{
    background: #005069;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor:pointer;
}
.save-button:hover{
    background: #0056b3;
}
.itinerary-overview{
   margin-top: 30px;
   padding: 20px;
   background: #fff;
   border-radius: 10px;
   box-shadow: 0px 4px 10px rgba(0,0,0,0.1);
   text-align: left;
}
.itinerary-overview h2{
text-align: center;
color:#005069;

}
.iframe-container{
    display: flex;
    justify-content: space-between;
    grid-gap:20px;
    gap:20px;
    margin-top: 20px;
}
.iframe-box{
    flex:1 1;
    background: #f9f9f9;
    padding: 10px;
    border-radius: 5px;
    text-align: center;

    
}
.iframe-box iframe{
    width:100%;
    height:300px;
    border:0;
    border-radius: 5px;
}
.finalize-button{
    background: #005069;
    color:white;
    padding: 10px 20px;
    border:none;
    border-radius: 5px;
    cursor:pointer;
    font-size: 16px;
    margin-top: 20px;
    width:100%;
}
.finalize-button:hover{
    background: #0056b3;
}
.contact{
    margin: 80px auto;
    max-width: 90%;
    display: flex;
    align-content: center;
    justify-content: space-between;
}
.contact-col{
    flex-basis: 48%;
    color: black;

}
.contact-col h3{
    color:#000f38;
    font-weight: 500;
    font-size: 25px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}
.contact-col h3 img{
    width:35px;
    margin-left: 10px;


}
.contact-col p{
    max-width: 450px;
    list-style: 0.3;
}
.contact-col ul li{
    display: flex;
    align-items: center;
    margin: 20px 0;


}
.contact-col ul li img{
    width: 25px;
    margin-left: 10px;


}
.contact form input,.contact form textarea{
    display: block;
    width: 100%;
    background: #ebecfe;
    padding: 15px;
    border: 0;
    outline: 0;
    margin-bottom: 15px;
    margin-top: 5px;
    resize: none;

}
.contact-col span{
    display: block;
    margin: 20px 0;
    
}

.footer{
    margin: 10px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #005069;
    padding: 15px 0;
}
.footer ul li{
    list-style: none;
    display: inline-block;
    margin-left: 20px;

}
